/* FAQ Container */
.faq-container {
  background-color: #fff;
  padding: 40px 0;
  text-align: center;
}

/* FAQ List */
.faq-list {
  max-width: 100%;
  margin: 20px;
  margin-bottom: -20px;
  text-align: left;
}

/* FAQ Item */
.faq-item {
  margin-bottom: 10px;
  border-bottom: 1px solid #eee;
  transition: all 0.3s;
  cursor: pointer;
  overflow: hidden;
}

.faq-item.open {
  max-height: 1000px;
}

/* FAQ Question */
.faq-question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #f5f5f5;
}

.faq-question h3 {
  margin: 0;
  font-size: 18px;
}

.faq-question span {
  font-size: 24px;
}

/* FAQ Answer */
.faq-answer {
  padding: 20px;
  background-color: #fff;
}

.faq-answer p {
  margin: 0;
  font-size: 16px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .faq-list {
    padding: 0 20px;
  }
}
