.services2-container {
  text-align: center;
  padding: 20px;
}

.services2-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.service2-row {
  display: flex;
  justify-content: space-evenly;
  flex-basis: calc(70%);
  margin-bottom: 15px;
}

.service2-card,
.service2-card1,
.service2-card2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  max-width: 100%;
  padding: 1%; /* Set padding as a percentage of the card size */
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  color: black;
  margin: 10px; /* Set margin as a percentage of the card size */
  width: 45%; /* Set initial width as a percentage */
  height: 500px;
  transition: all 0.3s ease; /* Add smooth transition */
  justify-content: space-between;
}

.service2-card:hover,
.service2-card1:hover,
.service2-card2:hover,
.service2-card3:hover {
  transform: scale(1.05); /* Scale up on hover */
}

.service2-card p,
.service2-card1 p,
.service2-card2 p{
  font-size: 100%;
}
.service2-card1 h3,
.service2-card2 h3,
.service2-card h3 {
font-size: 100%;
}

.apply-button {
  background-color: black;
  color: white;
  padding: 10px;
  padding-left: 15px;
  padding-right: 15px;
  text-decoration: none;
  border-radius: 5px;
}

.apply-button:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {


  .service2-card h3,
  .service2-card1 h3,
  .service2-card2 h3,
  .service2-card3 h3 {
    margin-top: -10px;
  }

  .services2-container {
    padding: 0px auto;
  }

  .services2-container p {
    margin-bottom: 15px;
  }
}



@media (max-width: 768px) {
  .service2-card, .service2-card1, .service2-card2, .service2-card3 {
    margin: 0px auto;
    margin-bottom: 10px;
    max-width: 100%;
    padding: 12%;
    margin-bottom: 10%;
  }

  .service2-row {
    display: flex;
    flex-direction: column;
  }

  .services2-list {
    height: 200%;
    display: flex;
    flex-direction: column;
  }
  .services2-container p{
    font-size: 110%;

  }
  .services2-container h1 {
    margin-top: 50px;
    font-size: 250%;
  }

  .service2-card h3,
  .service2-card1 h3,
  .service2-card2 h3{
    font-size: 150%;
  }

  
  .service2-card p,
  .service2-card1 p,
  .service2-card2 p,
  .service2-card3 p {
    font-size: 80%;
  }

  .service2-card a,
  .service2-card1 a,
  .service2-card2 a,
  .service2-card3 a {
    margin-top: 5px;
    margin-bottom: -15px;
  }

  .service2-card,
  .service2-card1,
  .service2-card2 {
    display:flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 0px auto;
    margin-bottom: 20px;
    height: 40%;
    width: 80%; /* Set a suitable percentage width for mobile */
  }

}