/* Home3.css */

.home3-container {
    max-width: 100%;
    margin: 2vh;
    padding: 20px;
    text-align: justify;
  }
  
  .dropdowns-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .dropdown {
    width: 100%;
    max-width: 100%;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: border-color 0.3s ease;
  }
  
  
  .dropdown-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 20px;
    background-color: #ffcc00; /* Yellow background color */
    color: #333; /* Text color */
    border: none;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease;
  }
  
  .dropdown-button:hover {
    background-color: #ff9900; /* Darker yellow on hover */
  }
  
  .course-details {
    padding: 10px 15px;
    background-color: #fff;
    border-top: 1px solid #ccc;
  }
  
  .course-details p {
    margin: 0;
  }
  
  .enquiry-button {
    margin-top: -15px;
    margin-bottom: 20px;
    display: inline-block;
    padding: 20px 30px;
    background-color: #333; /* Dark background color */
    color: #fff; /* Text color */
    border: none;
    border-radius: 4px;
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease;
  }
  
  
  .h2 h2 {
    margin-bottom: 10px;
    font-size: 2rem;
  }

  .our {
    text-align: justify;
    margin-bottom: 20px;
  }

  .enquiry-button:hover {
    background-color: #222; /* Slightly darker background color on hover */
  }
  
  
/* Media query for mobile devices */
@media screen and (max-width: 768px) {
  .h2 h2 {
    font-size: 3.8vw;
  }
  .home3-container p {
    font-size: 3.5vw;
    text-align: justify;
    text-align-last: auto;
  }
}