.verification-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0;
  text-align: center;

}

h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

label {
  font-weight: bold;
}

input[type="text"] {
  margin: 15px;
  width: 45%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

button {
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  padding: 10px 24px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #050607;
}


.verification-result {
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f8f8f8;
  width:100%;
}

.verification-result.loading {
  display: none;
  padding: 20px;
  font-weight: bold;
}

.verification-result p {
  margin: 10px 0;
}

.verification-result a {
  font-size: 20px;
  text-transform: uppercase;
  text-decoration: none;
  border: #f8f8f8;
  border-style: groove;
  background-color: #007bff;
  padding: 5px;
  color: #fff;
  font-weight: bold;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.verification-result a:hover {
  text-decoration-color: #f8f8f8;
  color: #fff;
  background-color: black;
}




.cin-form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.animation {
  display: flex;
  flex-direction: row;
  margin: 20px auto;
  width: 27.5%;
}


.form-first {
  max-width: 99%;

}
.animo {
  justify-content: space-between;
  display: flex; /* Display elements in a row */
  flex-direction: row; /* Horizontal layout */
  align-items: center; /* Center content vertically */
  margin: 5px;
  padding: auto;
  color: white;
  background-color: rgba(8, 7, 7, 0.853);
  height: 10rem auto;
  padding-left: 1rem;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
  max-width:100%;
  
}

.animo p {
  /* Add margin to create space between elements */
  margin: 0 20px; /* Adjust as needed */
  color: rgb(78, 137, 254) ;
  
}

.animo h3 {
  /* Add margin to create space between elements */
  margin: 0 20px; /* Adjust as needed */
}

.animo ul li {
  /* Add margin to create space between elements */ /* Adjust as needed */
  list-style: none;
  margin-bottom: 4px;
}

.lottie {
  max-width: 14%;
  margin: 0;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .animo {
    display: flex;
    flex-direction: column;
    padding: 2rem;
  }

  .lottie {
    max-width: 50%;
  }
  
  .animo ul li {
    margin: 0%;
    padding-right: 2.5rem;
  /* Add margin to create space between elements */ /* Adjust as needed */
    list-style: none;
    margin-bottom: 4px;
  }

  .animo h3 {
    /* Add margin to create space between elements */
    margin-bottom: 10px; /* Adjust as needed */
    
  }

  input[type="text"] {
    width: 80%
  }
  
  .form-first h2 {
    margin-top : 100px;
  }
}