/* Services.css */

.services-container {
    text-align: center;
    padding: 20px;
    background-color: rgb(253, 236, 84);
  }
  
  .services-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .service-card {
    background-color: #0a0a0a;
    border: 1px solid #ccc;
    color: #ccc;
    border-radius: 8px;
    padding: 30px;
    margin: 10px;
    width: 400px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .service-card h3 {
    font-size: 20px;
  }
  
  .service-card p {
    font-size: 16px;
  }
  
/* Media query for screens with a maximum width of 768px (e.g., mobile devices) */
@media (max-width: 768px) {
  .services-list {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center-align content on mobile */
  }

  .service-category {
    margin: 0px auto; /* Add spacing between categories on mobile */
  }

  .service-card {
    margin: 0px auto;
    width: 80%; /* Full width on mobile */
  }

  .service-card h3, .service-card p{
    font-size: 75%;
    margin: 5px auto;

  }
}
