/* Visitor.css */

.visitor-container {
  background-color: black;
  color: yellow;
  text-align: center;
  padding: 20px;
}

.visitor-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.counter {
  font-size: 2rem;
  margin: 10px;
  font-weight: bold;
  font-family: 'Arial', sans-serif; /* Use the desired font-family */
  text-transform: uppercase;
}

.counter h3 {
  font-size: 3rem;
  margin: 0;
  line-height: 1;
}

.counter p {
  margin-top: 5px;
  font-size: 1.2rem;
}
