/* Internship.css */

.internship-container {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  padding: 20px;
  box-sizing: border-box;
}

.header h1 {
  margin: 0;
  font-size: 1.8rem;
  background-color: black;
  color: hsl(0, 0%, 100%);
  padding: 2rem;
  font-family: 'Poppins', sans-serif;
  text-align: center;
}

.internship-scroll-container {
  display: flex;
  gap: 20px;
  box-sizing: border-box;
  height: 21.6rem;
}

.internship-card {
  flex: 0 0 auto;
  width: 275px;
  height: 350px;
  max-width: 100%; /* Allow cards to take full width on smaller screens */
  display: flex;
  flex-direction: column;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  font-size: 10%;
}

.internship-card p {
  margin: 10px;
  font-size: 0.75rem;
  text-align: justify;
  text-align-last: center;
}

.internship-card h2 {
  font-size: 1rem;
  margin-bottom: 10px;
  text-align: center;
  margin-top: -40px;
}

.internship-card img {
  max-width: 80%;
  align-self: center;
  margin-bottom: 10px;
  margin-top: -25px;
}

/* Media query for mobile devices */
@media screen and (max-width: 768px) {
  .internship-card {
    width:25%;
    max-width: 30%; /* Make cards take full width on mobile */
  }

  .header h1 {
    padding-top: 100px;
  }
}
