/* Home1.css */

.hero {
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
  padding: 15rem;
}

.hero h1 {
  font-size: 3rem;
  margin-top: -10rem;
  margin-bottom: 1.5rem;
}

.hero p {
  font-size: 1rem;
  font-weight: 600;
  max-width: 600px;
  margin: 0 auto 2rem;
}

.ctra button{
  padding: 10px;
  padding-left: 40px;
  padding-right: 40px;
}
.ctra1 button {

  padding: 10px;
  padding-left: 30px;
  padding-right: 30px;
  margin-left: 10px;
}
.cta-buttons {
  display: flex;
  justify-content: center;
  margin: 2.5rem;
}

.cta-button {
  background-color: #0a0801;
  color: #ffcc00;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  margin: 0 auto;
  margin-bottom: 20%;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: 0 0 5px rgba(20, 40, 5, 10); /* Custom box shadow with color #0a0801 */
}

.cta-button:hover {
  background-color: #ffcc00;
  color: #090909;
}

.why-choose-us {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 50px;
  background-color: #f8f9fa;
}


.why-choose-us ul {
  flex: 1;
  list-style-type: none;
  padding: 0;
  margin-top: -2rem;
}

.why-choose-us li {
  line-height: 1.2;
  font-size: 105%;
  margin-bottom: 10px;
  display: flexbox;
  text-align: justify;
}

.bullet {
  font-size: 1.5rem;
  margin-right: 10px;
  color: #ffcc00;
}

.why-choose-image img {
  max-width: 80%;
  height: auto;
}

/* Media query for mobile devices */
@media screen and (max-width: 768px) {
  .hero {
    padding: 5rem;
    padding-top: 20rem;
    max-width: 100%;
    background-position: 150%;
    background-size: 200%;
    
  }
  .hero h1 {
      font-size: 185%;
  }

  .hero p {
      font-size: 84%;
      text-align: justify;
      max-width: 100%;
  }

  .why-choose-us {
      flex-direction: column;
      padding: 30px;
  }

  .why-choose-us ul {
      width: 100%;
  }

  .why-choose-us li {
      font-size: 1rem;
  }

  .why-choose-image img {
    width: 100%;
    height: auto;
  }
}
