/* Footer Container */
.footer-container {
  background-color: black;
  color: white;
  padding: 40px 0;
  padding-bottom: -25px;
}

/* Footer Content */
.footer-content {
  display: flex;
  justify-content: space-around;
  max-width: 100%;
  padding: 10px;
}

/* Footer Sections */
.footer-section {
  flex: 1;
  margin-left: 50px;
  margin-right: 50px;
}

.footer-section h3 {
  font-size: 25px;
  color: yellow;
  margin-bottom: 10px;
}

.footer-section p {
  color: white;
  font-size: 18px;
}

.footer-section ul {
  list-style-type: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 10px;
}

.footer-section a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.footer-section a:hover {
  color: gray;
}

/* Footer Bottom */
.footer-bottom {
  background-color: black;
  color:white;
  text-align: center;
  font-size: 14px;
}

.footer-bottom p {
  font-size: 14px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-section {
    margin: 10;
    margin-bottom: 20px;
    font-size: 85%;
  }

  .footer-section h3
  {
    font-size: 1.2rem;
  }

  .footer-section p {
    font-size: 0.8rem;
    margin-bottom: -0.2rem;
  }
}
