/* home2.css */

.feature-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #3b3a3a;
  color: #fff;
  padding: 10px;
  max-width: 100%;
}

.feature-section h1 {
  color: #ffc107;
}

.feature-content {
  flex: 1;
}

.feature-content h2 {
  margin: 25px;
  font-size: 155%;
  margin-top: -10px;
  text-align: justify;
}

.feature-list {
  margin: 25px;
  font-size: 18.5px;
  list-style-type: none;
  padding-left: 0;
  text-align: justify;
}

.feature-item {
  margin-bottom: 10px;
  font-size: 80%;
}

.feature-bullet {
  color: #ffc107;
  margin-right: 10px;
  font-size: 18px;
}

.feature-image img {
  width: 100%;
  max-width: 400px;
  height: auto;
}

/* Add this to your CSS file */

.IMG-why {
  max-width: 65%; /* Set maximum width to 40% */
  margin: 0 auto; /* Center the element horizontally */
  display: block; /* Ensure it behaves as a block element */
}

@media screen and (max-width: 600px) {
  .feature-section {
    max-width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 40px; /* Adjusted padding for mobile */
  }

  .feature-content {
    font-size: 30px;
    padding: 0 auto; /* Adjusted padding for mobile */
    margin: 0 auto;
  }

  .feature-content h2  {
    text-align: center;
    margin: 20 auto;
    margin-top: 20px;
    font-size: 70%;
  }

  .feature-item {
    margin: 0 auto;
    margin-bottom: 10px;
    width: 100%;
    text-align: justify;
    line-height: 1.4;
    letter-spacing: 1px;
    font-size: 70%;
  }
}
