  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center
}


.msme p {
  margin-bottom: 20px; /* Add some space between the paragraph and the logo */
}

.msme .about-logo {
  width: 80%; /* Adjust the width of the MSME logo */
  margin: 0 auto; /* Center the element horizontally */
  display: flex;
  align-items: center;
  margin-top: 12.5%;
  margin-bottom: 14%;
  
}

/* ... (previous styles) */
.msme1 a {
  display: block;
  margin: 0 auto;
  width: 100% /* Add width to create a square element */
}

.msme1 h2 {
  margin-bottom: 25%;
}

.msme1 p {
  margin-top: 50px; /* Adjust margin-top for spacing */
}


