  flex: 1;
  text-align: right;
}
/* Add this to your CSS file */

.animation {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.anima {
  display: flex;
  align-items: center;
}

.anima-img {
  width: 50%; /* Reduce anima-img to 50% width */
  margin: 0 auto; /* Center anima-img horizontally */
}


.animation {
  justify-content: flex-start;
}

.animated-text p{
  font-size: 20px;
}

.animated-text h2{
  font-size: 25px; 
  text-align: center;
}


/* ... (rest of the styles) */

/* Responsive design for smaller screens */
@media (max-width: 768px) {
  .about-content {
    padding: 10px;
  }

  .about-content h1{
    font-size: 250%;
    margin: 0;
    margin-top: 40px;
    text-align: center;
  }

  .about-content p {
    font-size: 150%;
    text-align: justify;
    margin: 20px;
  }

  .banner-image {
    max-height: 100px;

  }

  .about-main {
    margin-top: -10px;
    display: flex;
    flex-direction: column;
  }

  .about-logo {
    width: 50%;
    max-width: 100%;
  }
  .content-item {
    width: 100%;
  }

  .animation {
    display: flex;
    flex-direction: column;
  }

  .anima {
    width: 500%;
    align-self: center;
    display: flex;
    flex-direction: column;
  }
  
  .anima-img {
    width:100%
  }

  .animated-text h2{
    margin:0 auto;
    font-size: 100%
  }
  .animated-text p{
    margin-top: 10px;
    font-size: 150%;
    line-height: 1;
  }


