/* Form.css */

.form-container {
  max-width: 700px;
  margin: 0 auto;
  padding: 25px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.form-content {
  text-align: center;
}

.input-field {
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.input-field[type="email"] {
  max-width: 80%;
  margin-top: 15px;
}

.input-field[type="text"] {
  width: 80%;
}

.form-content select {
  width: 83%;
  margin-top: 15px;
}


.submit-button:hover {
  background-color: #45a049;
}

.submit {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  margin-bottom: -12rem;
  margin-top: -2rem;
}

.submit h2 {
  font-size: 30px;
  margin-bottom: -3rem;
  margin-top: -10rem;
}

.submit-icon {
  width: 60%;
  margin-bottom: -25px;
}

.submit button {
  padding: 0.8rem;
  background-color: #45a049;
}

.back-button:hover {
  background-color: #090d0e;
}


/* Responsive Styles */
@media screen and (max-width: 600px) {
  .form-container {
    max-width: 90%;
  }

  .form-container h2 {
    font-size: 30px;
  }

  .form-container select {
    width: 85%;
    padding: 10px;
  }
  .submit-icon {
    width: 160%;
    margin-top: -2rem;
    margin-bottom: -4rem;
  }
  .submit {
    margin: 0.5rem;
    margin-top: -5rem;
  }

  .submit button {
    background-color: #45a049;
    margin-bottom: -18rem;
  }

  .submit button:hover {
    background-color: #090d0e;
  }
}