/* InternshipSection.css */

.internship-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  background-color: #f1fb33;
}

.banner img {
  width: 50rem;
  height: auto;
  border-radius: 10px;
  background-color: rgba(250, 244, 244, 0.748);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.intern-image{
  width: 100%;
}
.IMG {
  width: 80%;
}

.content {
  display: flex;
  flex-direction: row;
  text-align: center;
}

h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

p {
  text-align: center;
  justify-content: space-between;
  font-size: 16px;
  line-height: 1.3;
  text-align-last: center;
}

.domains {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
}

.domain {
  padding: 20px;
}

h3 {
  font-size: 16px;
  margin-bottom: 10px;
}

/* Add more CSS styles as needed */
/* InternshipSection.css */

@media screen and (max-width: 768px) {
.internship-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2vw; /* Adjust gap for responsiveness */
  padding: 3vw; /* Adjust padding for responsiveness */
  background-color: #f1fb33;
}

.intern-image {
  width: 90%; /* Adjust width for responsiveness */
  max-width: 50rem; /* Set maximum width for larger screens */
  height: auto;
  border-radius: 10px;
  background-color: rgba(250, 244, 244, 0.748);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  margin: 20px;
}

.content {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
  max-width: 800px; /* Set maximum width for larger screens */
}

.content p {
  font-size: 1rem;
  text-align: justify;
  margin: 10px;
  text-align-last: center;
}

h2 {
  font-size: 3.5vw; /* Adjust font size for responsiveness */
  margin-bottom: 2vw;
}

p {
  font-size: 1.5vw; /* Adjust font size for responsiveness */
  line-height: 1.5;
}

.domains {
  display: flex;
  flex-direction: column; /* Adjust to display domains in a column for mobile */
  justify-content: center;
  gap: 2vw; /* Adjust gap between domains for responsiveness */
}

.domain {
  padding: 2vw; /* Adjust padding for responsiveness */
}

h3 {
  font-size: 5vw; /* Adjust font size for responsiveness */
  margin-bottom: 15px;
}
}