/* Contact.css */
/* Main container styles */
.contact-container {
    padding: 20px;
    color: #fff;
    text-align: center;
    position: relative; /* Add position relative to enable overlay */
}

/* Add overlay with black color */

.contact-container::after {
    content: ''; /* Change 'background' to an empty string */
    background: url('../../assets/contact-bg.jpg') center/cover no-repeat;
    position: absolute;
    opacity: 80%;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

  /* Rest of your styles remain the same */

  .contact-container h1 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  /* Contact info styles */
  .contact-info {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 20px auto;
  }
  
  .info-item {
    display: flex;
    flex: wrap;
    flex-direction: row;
    background-blend-mode: darken;
    padding: 20px;
    margin: 10px;
  }
  
  .info-item p {
    font-size: 22px;
    margin: 10px 0;
  }
  
  .icon {
    font-size: 35px;
    color: black;
    
    margin: 10px auto;
    margin-right: 20px;
    margin-left: 10px;
  }
  
  .info-item p{
    text-shadow: black;
    
    color: white;
    font-weight: 700;
  }

  /* Contact form styles */
  .contact-form {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    padding: 40px;
    margin: 20px auto;
    width: 60%;
    text-align: center;
  }
  
  .contact-form h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  .contact-form input[type="name"],
  .contact-form input[type="email"],
  .contact-form input[type="tel"],
  .contact-form textarea {
    width: 100%;
    padding: 0;
    margin: 0;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-bottom: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;  
  }
  ::placeholder {
    text-align: left;
    padding-left: 10px;
  }
  
  .contact-form button {
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    transition: background-color 0.3s;
  }
  
  .contact-form button:hover {
    background-color: #0056b3;
  }

  ::placeholder {
    margin-left: 20px;
  }
  
  /* Testimonials styles */
.testimonials {
    border-radius: 10px;
    padding: 20px;
    margin: 20px auto;
    width: 70%;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.2);
  
  }
  
  .testimonials h2 {
    color: rgb(247, 241, 241);
    text-transform: uppercase;
    font-size: 30px;
    margin-bottom: 20px;
    padding: 0;
    margin: 0;
  }
  
  .testimonials ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Center testimonials horizontally */
  }
  
  .testimonials li {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    padding: 15px;
    margin: 10px;
    width: 45%; /* Two comments in one row */
    display: flex;
    flex-direction: column; /* Display name and content vertically */
    align-items: center; /* Center content horizontally */
    text-align: center;
  }
  
  .testimonial-author {
    font-style: italic;
    margin-top: 10px;
  }
  
  .testimonial-content p {
    color: #fff;
    margin: 10px auto;
  }
  
  .testimonial-profile img {
    border-radius: 50%;
  }
  
  .testimonial-rating {
    color: yellow;
  }
  
  .info-item {
    display: flex;
    flex: wrap;
    flex-direction: row;
    padding: 20px;
    margin: 10px;
    border-radius: 10px;
    transition: transform 0.3s; /* Add hover transition */
  }
  
  .info-item:hover {
    transform: scale(1.05); /* Hover effect - increase size */
  }
  
  .info-item p {
    text-shadow: 2px 2px 4px black; /* Shadow effect */
    color: rgb(249, 245, 245); /* Text color */
    font-weight: 700;
    transition: color 0.3s; /* Add hover transition for text color */
  }
  
  .info-item:hover p {
    color: white; /* Text color on hover */
  }


  @media screen and (max-width: 768px) {

    .contact-container {
        margin: 0;
        max-width: 100%;
        position: relative;
        flex-direction: column;
        align-items: center;
        text-align: center;
        }

    .contact-container h1 {
      margin-top: 80px;
    }
  
    .contact-form {
      width: 80%; /* Adjust form width for smaller screens */
    }
  
    .testimonials {
      width: 90%; /* Adjust testimonials width for smaller screens */
    }

    .testimonials ul li {
        padding: 0;
        margin: 0;
        padding: 10px;
        padding-left: 100px;
        padding-right: 100px;
        padding-top: 20px;
        padding-bottom: 20px;
        margin-bottom: 10px;
    }
  
    .testimonial-comment{
        text-align: center;
        margin: 0;
        margin: -10px
    }

    .testimonial-content p {
      font-size: 65%;
    }

    .info-item {
      width: 100%;
      margin: 0 auto; /* Adjust info item width for smaller screens */
    }
    

    .info-item p{
        font-size: 1.2rem;
        text-align: justify;
    }

    .icon {
        font-size: rem;
    }
  }